.top-sec-subtitle{
  color:#E83828;
  font-size:4.2vw;
  font-weight:100;
  padding:3.1vw 0;
}

.top-sec-content{
  font-size:2vw;
  font-weight:100;
}

.top-course{
    text-align:center;
    background-image:url(../img/top/course-back.png);
    background-size: cover;
    background-position:center;
    background-repeat: no-repeat;
    position:relative;

  .top-course__li{
    display:flex;
    flex-wrap:wrap;
    justify-content:center;
  

    .top-course__li__item{
      list-style:none;
      margin:4.4vw 1.3vw 0;
      width:43%;
      background:#fff;
      padding-bottom:16vw;
      position:relative;
      border-radius:1.5vw;

      .top-course__item__img img{
        border-radius:1.5vw 1.5vw 0 0;
      }

      .top-course__item__title{
        height:15vw;
        display:flex;
        align-items:center;
        justify-content:center;
        font-size:3.3vw;
        font-weight:100;
      }

      .top-course__item__content{
        font-size:2.8vw;
        font-weight:100;
      }

      .top-course__btn{
        position:absolute;
        bottom:5.9vw;
        left:0;
        right:0;
      }

      &:after{
        content: '';
        position: absolute;
        width:9.5vw;
        height:2.5vw;
        left:calc(50% - 4.75vw);
        bottom: 0;
        z-index:1;
        border-radius:0.8vw 0.8vw 0 0;
      }
    }

    .top-course__eclil{
      .top-course__item__title{
        color:#E54C3C;
      }
    
      .top-viewmore,&:after{
        background:#E54C3C;
      }
    }

    .top-course__finance{
      .top-course__item__title{
        color:#6E458F;
      }
    
      .top-viewmore,&:after{
        background:#6E458F;
      }
    }

    .top-course__steam{
      .top-course__item__title{
        color:#00B8B8;
      }
    
      .top-viewmore,&:after{
        background:#00B8B8;
      }
    }

    .top-course__experience{
      .top-course__item__title{
        color:#595757;
      }
    
      .top-viewmore,&:after{
        background:#595757;
      }
    }
  }
}